body.loading {
  overflow: hidden;
}

.preload {
  position: fixed;
  inset: 0;
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: 100vh;
  z-index: 999;
  visibility: visible;
  opacity: 1;
  transition: 0.5s ease;
}
.preload.loaded {
  visibility: hidden;
  opacity: 0;
}

.preload__text {
  bottom: 10px;
  left: 0;
  position: absolute;
  font-size: 24px;
  width: 50px;
  white-space: nowrap;
  padding-block: 20px;
  margin: 0;
  color: rgb(255, 255, 255);
  text-shadow: 2px 2px 5px rgb(0, 0, 0);
  padding-inline: 10px;
  border-radius: 50px;
  text-align: center;
  background: linear-gradient(rgba(0, 128, 0, 0.24), green, rgba(0, 128, 0, 0.405));
}
@media (max-width: 600px) {
  .preload__text {
    font-size: 2px;
    left: -50px;
    padding: 10px;
  }
}
@media (max-width: 400px) {
  .preload__text {
    font-size: 18px;
    left: -20px;
  }
}

.load__img {
  position: absolute;
  top: 120px;
  left: 60px;
  animation: rotate 2s infinite;
  width: 100%;
  max-width: 200px;
  height: auto;
}
@media (max-width: 600px) {
  .load__img {
    top: 160px;
    max-width: 150px;
    left: 50px;
  }
}

.preload__items {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1001;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 350px;
  height: 400px;
}
@media (max-width: 600px) {
  .preload__items {
    width: 250px;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
